const Movie = props => {
  return (
    <li key={props.movie.imdbID} className="list-group-item">
      {props.movie.imdbID}
      <p>{props.movie.Title}</p>
      <p>{props.movie.Year}</p>
      <img style={{ height: "200px" }} src={props.movie.Poster}></img>
    </li>
  );
};
export default Movie;
